<style>:root{--logo-height:60px;--logo-width:140px;--logo-bar-bottom-padding:0.5rem}.logo{height:var(--logo-height);width:var(--logo-width);margin-right:.5rem;float:right}.logo,h2,h4{display:inline-block;margin:0}h2{margin-top:.9rem}h4{float:right;margin-top:1rem;margin-right:3rem}.logo-bar{height:calc(var(--logo-height) + var(--logo-bar-bottom-padding));padding-bottom:var(--logo-bar-bottom-padding)}label{width:calc(100% - var(--logo-width) - 1rem)}input,label{display:inline-block}input{width:80%;font-weight:700;font-style:italic}</style>

<script>
    import Logo from "./logo.svg"
    import {sheetName} from '../../../applicationSettings'
    let worksheet_name="";
    export let worksheet;
    export let pageIndex=0;
    const unsubscribe = sheetName.subscribe(value => {
        worksheet_name = value;
    });

</script>
<div class="logo-bar">
<div class="logo">
    <Logo height="100%" width="100%"/>
</div>
<h2>{worksheet_name}</h2>
    {#if worksheet.pageCount > 1}
        <h4>Page {pageIndex+1}</h4>
    {/if}
<label for="name-input" >Worksheet Name:
    <input id="name-input" type="text" bind:value={worksheet.name} />
</label>
</div>